import { Component, OnInit } from '@angular/core';

import { PageService } from '../services/page.service';
import { SettingService } from '../services/setting.service';

@Component({
  selector: 'app-page-user-panel',
  templateUrl: './page-user-panel.component.html',
  styleUrls: ['./page-user-panel.component.css']
})
export class PageUserPanelComponent implements OnInit {

  constructor(
    private pageService: PageService,
    private settingService: SettingService
  ) { }

  ngOnInit() {
  }

  subpageTranslation() {
    if (this.pageService.getSubpage()=='Profile') return '';
    else if (this.pageService.getSubpage()=='Edit profile') return '';
    else if (this.pageService.getSubpage()=='History') return '';
    else if (this.pageService.getSubpage()=='Setting') return '';
    else return '';
  }

}



